.map {
  position: relative;
}

.map__image {
  @media (min-width: @tablet-width) {
    width: 768px;
    height: 400px;
  }

  @media (min-width: @desktop-width) {
    width: 100%;
  }
}

.map__icon {
  position: absolute;
  left: 0;
  top: 0;

  @media (min-width: @desktop-width) {
    top: 40%;
    left: 49%;
  }
}

.map iframe {
  border: none;
  display: block;
}
